          <br />
          <span class="production-descr-second"
            >      :</span
          >
          <br />
             , ,    
            
        </p>
      </template>
      <template v-slot:button>
        <CentrisBtn class="production_btn" @click="openModal" />
      </template>
      <template v-slot:img>
        <img src="../assets/images/video-images/bg1.webp" alt="" />
      </template>
      <template v-slot:imgMob>
        <img src="../assets/images/video-images/bg2.webp" alt="" />
      </template>
