.swiper-wrapper {
}
.swiper-slide {
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  transform: scale(1);
  transition: transform 0.5s;
  padding: 0 5px;
  @media (max-width: 768px) {
    max-width: 100%;
